<template>
  <div>
    Click to add a target point <span class="target">&#11044;</span>. Hold down <kbd>shift</kbd> to create source point <span class="source">&#11044;</span>. Epsilon: <input v-model="epsilonInput" placeholder="Epsilon" @change="handleEpsilonChange" /> Iters: <input v-model="itersInput" placeholder="Iters" @change="handleItersChange" />  <button @click="removeAllPoints" > Remove all points</button>

    <br />
    Sinkhorn dist: {{ sinkhornDist }}. 
    
    <v-stage ref="stage"
      :config="stageSize"
      @click="handleClick"
    >
      <v-layer ref="layer">
        <v-circle
          v-for="item in sources"
          :key="item.id"
          :config="{
            x : item.x, y: item.y, radius: 5, fill: 'blue', id: item.id,
            draggable: true
          }"
          @dragstart="handleDragstart"
          @dragmove="handleDragmove"
          @dragend="handleDragend"
        ></v-circle>
      </v-layer>
      <v-layer ref="layer2">
        <v-circle
          v-for="item in targets"
          :key="item.id"
          :config="{
            x : item.x, y: item.y, radius: 5, fill: 'red', id: item.id,
            draggable: true
          }"
          @dragstart="handleDragstart"
          @dragmove="handleDragmove"
          @dragend="handleDragend"
        ></v-circle>
      </v-layer>
      <v-layer ref="layer3">
        <v-line
          v-for="arr in arrows"
          :key="arr.id"
          :config="{
            points : arr.points,
            stroke: 'black'
          }"
        ></v-line>
      </v-layer>
      <v-layer ref="dragLayer"></v-layer>
    </v-stage>
  </div>
</template>

<script>
const tf = require('@tensorflow/tfjs');

const width = window.innerWidth;
const height = window.innerHeight;

export default {
  data() {
    return {
      epsilonInput: 0.01,
      itersInput: 10,
      iters: 10,
      grad_sink_fn: x => x,
      sink_fn: x => x,
      sources: [],
      targets: [],
      arrows: [],
      sinkhornDist: 0.0,
      stageSize: {
        width: width,
        height: height
      }
    };
  },
  methods: {
    handleEpsilonChange() {
      this.calculate();
    },
    handleItersChange() {
      this.calculate();
    },
    handleDragstart(evt) {
      this.dragItemId = evt.target.id();
    },
    handleDragmove(evt) {
      const item = this.sources.find(i => i.id === this.dragItemId) || this.targets.find(i => i.id === this.dragItemId);
      item.x = evt.target.x();
      item.y = evt.target.y();
      this.calculate();
    },
    handleDragend(evt) {
      const item = this.sources.find(i => i.id === this.dragItemId) || this.targets.find(i => i.id === this.dragItemId);
      item.x = evt.target.x();
      item.y = evt.target.y();
      this.calculate();

      this.dragItemId = null;
    },
    handleClick(evt) {
      const stage = evt.target.getStage();
      var pos = stage.getPointerPosition();
      pos.id = this.nextId;
      this.nextId += 1;

      if (event.shiftKey) {
        this.sources.push(pos);
      } else {
        this.targets.push(pos);
      }

      this.save();
      this.calculate();
    },
    removeAllPoints() {
      this.sources = [];
      this.targets = [];
      this.arrows = [];
      this.calculate();
    },
    updateInputs() {
      const sources_arr = this.sources.map(item => [item.x, item.y] );
      const targets_arr = this.targets.map(item => [item.x, item.y] );

      if (sources_arr.length > 0 && targets_arr.length > 0) {
        this.sources_tf = tf.tensor2d(sources_arr);
        this.targets_tf = tf.tensor2d(targets_arr);
      }

      this.iters = parseInt(this.itersInput);
      this.epsilon_tf = tf.tensor(parseFloat(this.epsilonInput));
    },
    distanceMatrix(x, y) {
      return tf.sum(tf.square( tf.expandDims(x, 1).sub(tf.expandDims(y, 0)) ), 2);
    },
    calculate() {

      this.updateInputs();

      this.sinkhornDist = this.sink_fn(this.sources_tf, this.targets_tf);
      const gr = this.grad_sink_fn(this.sources_tf).neg();

      let arrs = [];
      for (let i = 0; i < this.sources.length; i++) {
        let s = this.sources[i];
        arrs.push({points: [s.x, s.y, s.x+gr.arraySync()[i][0], s.y+gr.arraySync()[i][1]], id: i});
      }
      this.arrows = arrs;
    },

    load() {
      this.nextId = 1;

      return;
      //const sources = localStorage.getItem('sources') || '[]';
      //this.sources = JSON.parse(sources);

      //const targets = localStorage.getItem('targets') || '[]';
      //this.targets = JSON.parse(targets);
    },

    save() {
      localStorage.setItem('sources', JSON.stringify(this.sources));
      localStorage.setItem('targets', JSON.stringify(this.targets));
    },
    
    sinkhornStep(C, f, epsilon) {

      var g = tf.logSumExp(tf.neg(f).sub(tf.transpose(C)).div(epsilon), 1).mul(epsilon);
      f = tf.logSumExp(tf.neg(g).sub(C).div(epsilon), 1).mul(epsilon);
      return [f, g];
    },

    sinkhorn(C, epsilon, niter) {

      const n = C.shape[0];
      var f = tf.zeros([n]);
      var fg, g;
      for (var i = 0; i < niter; i++)  {
        fg = this.sinkhornStep(C, f, epsilon);
        f = fg[0];
        g = fg[1];
      }

      const P = (tf.expandDims(tf.neg(f), 1).sub(tf.expandDims(g, 0)).sub(C)).div(epsilon);
      
      var OT = tf.sum(tf.exp(P).mul(C));
      return OT;
    },

    initGraph() {

      this.sources_tf = tf.tensor2d([[0,0]], [1,2]);
      this.targets_tf = tf.tensor2d([[0,0]], [1,2]);

      this.sink_fn = (s, t) => this.sinkhorn(this.distanceMatrix(s, t), this.epsilon_tf, this.iters);
      this.sink_from_sources_fn = x => this.sink_fn(x, this.targets_tf);
      this.grad_sink_fn = tf.grad(this.sink_from_sources_fn);
    }

  },
  mounted() {
    this.initGraph();
    this.load();
  }
};
</script>

<style>
  .target { color: red; }
  .source { color: blue; }

</style>

